.usersContainer {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 10px;
    color: rgb(210, 210, 210);
}
.users {
    align-items: center;
    justify-content: center;
    display: flex;
    flex-wrap: wrap;
    gap: 10px;
}
.user {
    display: flex;
    align-items: center;
}
.user img {
    width: 35px;
    height: 35px;
}
.avaAndButton {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 10px;
}
.info {
    display: flex;
    justify-content: space-between;
    border: 1px solid darkslategrey;
    width: 100%;
    height: 50px;
    margin: 10px;
    border-radius: 10px;
    padding: 10px;
}
.userNameAndStatus {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}
.userAddress {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    justify-content: center;
}
.selectedPage {
    font-weight: bold;
}